/* ############# STYLES TO MAX WIDTH OF 64 REM (1024px) ################# */
@media (max-width: 64rem) {
  /* ############### CONTENT CONTAINERS ################# */
  .leaf-admin-content {
    margin: 1rem;
  }

  .leaf-show-opts {
    font-size: 0.9rem;
    margin-top: 1.2rem;
    cursor: pointer;
  }

  .leaf-center-content {
    width: calc(100% - 2rem);
    margin: 0.6rem auto 0.6rem auto;
    font-family: "Public Sans Regular";
    white-space: nowrap;
  }

  /* ############### USWDS OVERRIDES ################# */
  .usa-logo {
    margin: 0;
    height: 4.6rem;
    float: left;
  }

  .usa-logo a {
    color: #fff;
    text-decoration: none;
  }

  .usa-navbar {
    border-bottom: 0;
    width: 100%;
  }

  .default-container {
    width: 100%;
  }

  .sidenav {
    display: block;
    float: left;
    margin: 0.6rem 1rem;
    padding: 1rem;
  }

  .sidenav:hidden {
    display: block;
    float: left;
    background: none;
    width: 15rem;
    background-color: none;
    box-shadow: none;
  }

  .sidenav:empty {
    display: block;
    float: left;
    background: none;
    width: 15rem;
    background-color: none;
    box-shadow: none;
    min-height: 1px;
  }

  .sidenav-right {
    display: block;
    float: right;
    margin: 0.6rem 1rem;
    padding: 1rem;
  }

  .sidenav-right:hidden {
    display: block;
    float: right;
    background: none;
    width: 15rem;
    background-color: none;
    box-shadow: none;
  }

  .sidenav-right:empty {
    display: block;
    float: right;
    background: none;
    width: 15rem;
    background-color: none;
    box-shadow: none;
    min-height: 1px;
  }

  .usa-banner__header {
    padding-bottom: 0.2rem;
    padding-top: 0.2rem;
    min-height: 0;
  }

  .usa-banner__header-close-text, .usa-banner__header-text {
    font-size: 0.6rem;
    letter-spacing: 0.01rem;
  }

  h1, h2, h3, h4, h5, h6 {
    margin: 0;
    color: #3d4551;
  }

  h3 {
    margin: 0.8rem 0;
  }

  .main-content {
    right: 0;
    bottom: 0;
    display: inline-block;
    position: relative;
    width: calc(100% - 15rem);
    white-space: normal;
  }

  /* ############### LEAF HEADER ################# */
  .site-header {
    background-color: #252f3e;
    height: 3.8rem;
    box-shadow: 0px 3px 4px #a9aeb1;
  }

  .leaf-site-title {
    color: #fff;
    display: inline-block;
    font-size: 1rem;
    margin-top: 0.9rem;
    font-family: "Public Sans Bold";
  }

  .leaf-header-description {
    font-family: "Public Sans Thin";
    font-size: 0.9rem;
    display: block;
    font-family: "Public Sans Regular";
  }

  .leaf-logo {
    float: left;
  }

  .leaf-logo img {
    width: 52px;
    margin: 0.2rem 0.5rem 0 0.2rem;
  }

  .leaf-header-right {
    margin-top: 0.5rem;
    float: right;
    color: #fff;
    font-family: "Public Sans Regular";
    text-align: right;
  }

  .lf-alert {
    font-size: 0.8rem;
    background-color: #e41d3d;
    color: #f6f6f2;
    padding: 0.2rem;
    font-family: "Public Sans Medium";
  }

  .usa-logo {
    font-size: 1.2rem;
    line-height: 1.1rem;
    margin: 1.6rem 0 0 0.2rem;
    white-space: nowrap;
  }

  #nav {
    margin: 1.4rem 2.4rem 0 0;
    font-size: 0.7rem;
  }

  /* ############### widths by rems ################# */
  .leaf-width-1rem {
    width: 1rem;
  }

  .leaf-width-2rem {
    width: 2rem;
  }

  .leaf-width-3rem {
    width: 3rem;
  }

  .leaf-width-4rem {
    width: 4rem;
  }

  .leaf-width-5rem {
    width: 5rem;
  }

  .leaf-width-6rem {
    width: 6rem;
  }

  .leaf-width-7rem {
    width: 7rem;
  }

  .leaf-width-8rem {
    width: 8rem;
  }

  .leaf-width-9rem {
    width: 9rem;
  }

  .leaf-width-10rem {
    width: 10rem;
  }

  .leaf-width-11rem {
    width: 11rem;
  }

  .leaf-width-12rem {
    width: 12rem;
  }

  .leaf-width-14rem {
    width: 14rem;
  }

  .leaf-width-24rem {
    width: 24rem;
  }

  /* ############### GLOBAL NAV ################# */
  #nav {
    margin: 1.1rem 2.5rem 0 0;
    min-height: 32px;
    white-space: nowrap;
  }
}